@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    padding: 0px;
    margin: 0px;
    list-style: none;
    quotes: none;
    text-decoration: unset;
    outline: none;
    border: none;
    box-sizing: border-box;
}

html {
    display: flex;
    justify-content: center;
    margin: auto;
    height: 1920px;
    width: 100vw;
}

body {
    font-family: "Poppins", "Quicksand", "Lora", sans-serif;
    box-sizing: border-box;
    overflow: hidden;
    margin: 0 auto;
    padding: 0;
    /* position: relative; */
    /* background-image: url("%PUBLIC_URL%/img/masjid.jpg"); */
}

@keyframes hoverNav {
    0% {
        background-color: white;
    }

    100% {
        background-color: rgb(134, 60, 0);
    }
}

@keyframes munculTotal {
    0% {
        transform: scale(0%);
        /* display: none; */
    }

    100% {
        transform: scale(100%);
    }
}

@keyframes kedut {
    0% {
        transform: scale(100%);
    }

    100% {
        transform: scale(95%);
    }
}

@keyframes typing {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}

@keyframes fade {
    from {
        opacity: 0.4;
    }

    to {
        opacity: 1;
    }
}

#home {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    color: white;
    padding: 20px;
}


#cover h1,
#home h1 {
    transform: scale(0%);
    animation: munculTotal 1s ease-in-out 1,
        kedut 1s ease-in-out infinite alternate;
    animation-delay: 0s, 1s;
}


