/* ----- HOME END ----- */

/* PESAN START */
#home div>div,
.pesan,
#pesan h1,
#gallery h1,
#gallery #images,
.dot {
    transform: scale(0%);
    animation: munculTotal 1s ease-in-out 1 forwards;
    animation-delay: 0s, 1s;
}

/* PESAN END */

.fade {
    animation-name: fade;
    animation-duration: 1.5s;
}

.dot {
    cursor: pointer;
    height: 10px;
    width: 10px;
    margin: 0 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

.dot.active {
    @apply bg-orange-400;
}

::-webkit-scrollbar {
    @apply w-[1vh]
}

::-webkit-scrollbar-track {
    @apply bg-white;
}

::-webkit-scrollbar-thumb {
    @apply bg-orange-600;
}

::-webkit-scrollbar-thumb:hover {
    @apply bg-orange-400;
}



.prev,
.next {
    cursor: pointer;
    position: absolute;
    width: max-content;
    padding: 16px;
    color: white;
    font-weight: bold;
    font-size: 25px;
    z-index: 10;
    height: 100%;
    transition: 0.3s ease-in-out;
    animation: munculTotal 1s ease-in-out 1 forwards;
    /* top: 40%; */
    /* user-select: none; */
}

.next {
    right: 0;
    border-radius: 0 10px 10px 0;
}

.prev {
    left: 0;
    border-radius: 10px 0 0 10px;
}

.prev:hover,
.next:hover {
    color: rgb(251, 146, 60);
    background-color: rgba(0, 0, 0, 0.2);
}